import io from 'socket.io-client';
import { each, Stream } from './Mason'

class ModelServer {
    constructor(location){
        this.socket = io(location)
    }


    connect(){
        return new Promise( resolve => {
            this.socket.emit('fetch-models', {}, ( { models } ) => {
                each(models, ( name, methods) => {
                    const Model = new Object
                    methods.forEach( method =>{
                        Model[method] = (...args) => {
                            return new Stream( resolve => {
                                this.socket.emit(`${name}-${method}`, args, id => {
                                    this.socket.on(id, result => {
                                        resolve(result)
                                    })
                                })
                            })
                        }
                    })
                    this[name] = Model
                })
                resolve()
            })
        })
    }

}

export const Models = new ModelServer('https://hidden-haven-quest.triframe.cloud');

export const server = {
    connect: async function(){
        await Models.connect()
    }
}