import React, { Component } from 'react';
import { Models } from '../server'
import { QRScanner } from '../components/QRScanner';

const { Users, Rounds, Challenges } = Models
export class PlayerPortal extends Component {

    state = {

    }

    componentDidMount(){
        this.fetchUser()
    }

    async fetchUser(){
        Users.subscribe(this.props.match.params.userID)
            .then(user => {
                this.fetchChallenge(user.challengeID)
            })
    }

    async fetchChallenge(challengeID){
        let challenge = await Challenges.find(challengeID)
        this.setState({ challenge, lastAttempt: null })
    }

    render() {
        const { lastAttempt, challenge } = this.state
        if(!challenge) return <h1>Awaiting Challenge...</h1>
        return (
            <div>
                <h1>{challenge.description}</h1>
                <div>
                { lastAttempt === false &&
                    <h3 className='text-danger'>Incorrect, please try again...</h3>
                }
                { lastAttempt === true &&
                    <h3 className='text-success'>Challenge Complete!</h3>
                }
                </div>
                <QRScanner onInput={this.attempt}/>
            </div>
        );
    }

    attempt = async (code) => {
        if(this.state.challenge){
            let result = await Challenges.solve(this.state.challenge.id, code)
            this.setState({ lastAttempt: result })
        }
    }
}

