import React, { Component } from 'react';
import jsQR from "jsqr";
import { ImageCapture } from '../ImageCapture'

export class QRScanner extends Component {

    componentDidMount(){
        this.startQRScanner()
    }

    async startQRScanner(){
        const mediaStream = await navigator.mediaDevices.getUserMedia({video: { facingMode:  "environment"  } })
        const canvas = document.createElement('canvas')
        const ctx = canvas.getContext('2d')
        const track = mediaStream.getVideoTracks()[0];
        let imageCapture = new ImageCapture(track, this.refs.video);
        let video = imageCapture.videoElement
        document.body.append(video)
        let scan = async () => {
            canvas.width = video.videoWidth
            canvas.height = video.videoHeight
            ctx.drawImage(video, 0, 0, video.videoWidth, video.videoHeight)
            if(!video.videoWidth || !video.videoHeight) return setTimeout(scan, 10)
            let imageData = ctx.getImageData(0, 0, video.videoWidth, video.videoHeight);
            let code = jsQR(imageData.data, video.videoWidth, video.videoHeight)
            if(code && typeof this.props.onInput === 'function') this.props.onInput(code.data) 
            setTimeout(scan, 10)
        }
        scan()  
    }

    render() {
        return (
            <video ref="video"/>
        );
    }
}

