import React, { Component } from 'react';
import { Models } from '../server'
import { is } from '../Mason'

const { Users, Rounds } = Models

export class Dashboard extends Component {

    state = {
        round: null,
        selectedChallengeID: null,
        users: []
    }

    componentDidMount(){
        this.fetchCurrentRound()
        this.fetchUsers()
        Rounds.beginGame()
    }

    async fetchCurrentRound(){
        Rounds.current()
            .then( round => {
                this.setState({ round, selectedChallengeID: null })
            })
    }

    async fetchUsers(){
        Users.all()
            .then( users => {
                this.setState({ users })
            })
    }

    render() {
        let { users, round, selectedChallengeID } = this.state
        if(!round) return <h3>Loading...</h3>
        if(round.complete) return <h3>You Win!</h3>
        return (
            <div>
                <h1>{round.name}</h1>
                <ul className="nav nav-pills">
                    { round.challenges.map( challenge => (
                        <li key={challenge.id} className="nav-item">
                            <a href="javascript:void(0)" onClick={() => this.select(challenge.id)} className={`nav-link ${is('active').when(selectedChallengeID === challenge.id)}`} >
                                {challenge.description}
                            </a>
                        </li>
                    ))}
                </ul>
            <div className="card-deck">               
                {users.map( user => (
                    <div key={user.id} className={`card ${is('border-primary').when(user.challengeID === selectedChallengeID)}`} style={{width: '18rem', cursor: 'pointer'}} onClick={() => this.assign(user)}>
                        <div style={{ height: '100px', overflow: 'hidden' }}>
                            <img className="card-img-top" src={user.image} alt="Card image cap"/>
                        </div>
                        <div className="card-body">
                            <h5 className="card-title">{user.name}</h5>
                        </div>
                    </div>
                ))}
                </div>
            </div>
        );
    }

    // Reducers
    select(challengeID){
        this.setState({ selectedChallengeID: challengeID})
    }

    async assign(user){
        const { selectedChallengeID } = this.state
        if(user.challengeID != selectedChallengeID) await Users.update(user.id, { challengeID: selectedChallengeID })
        else await Users.update(user.id, { challengeID: null })
    }
}

